import { deepMerge, getGetParamsAsString } from './utils';

class Api {
  #context: IContext;
  #baseUrl: string;
  constructor({
    url,
    calls,
    context = {},
  }: {
    url: string;
    calls: ICalls;
    context: RequestInit;
  }) {
    this.#baseUrl = url;
    this.#context = context;
    Object.keys(calls).forEach((key) => {
      this[key] = this.call(calls[key]);
    });
  }

  private call = (callData: ICall) => {
    const finalContext = deepMerge(this.#context, callData.context);
    const query = callData.query;
    const finalUrl = this.#baseUrl + callData + getGetParamsAsString(query);
    return async function () {
      const result = await fetch(finalUrl, finalContext);
      if (result.status === 200) {
        const json = await result.json();
        return json;
      }
    };
  };
}

export default Api;
